
footer {
    background: var(--color-primay);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a {
    color: var(--color-bg);
}
.footer-logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.footer-link {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;

}
.footer-socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}
.footer-socials a {
    background: var(--color-bg);
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    transition: var(--transition);
}
.footer-socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}
.footer-copy {
    margin-bottom: 4rem;
    color: var(--color-bg);
}




/* ===================================== */


@media screen and (max-width:600px) {
    .footer-link {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer-socials {
        margin-bottom: 2.6rem;
    }
}