.about-container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;

}
.about-me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primay), transparent);
    display: grid;
    place-items: center;
}
.about-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about-image:hover{
    transform: rotate(0);
}
.about-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}
.about-card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.4rem;
    text-align: center;
    transition: var(--transition);
}
.about-card:hover {
    background: transparent;
    border-color: var(--color-primay-variant);
    cursor: pointer;
}

.about-icon {
    color: var(--color-primay);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about-card h5 {
    font-size: 0.9rem;
}
.about-card small {
    font-size: 0.7rem;
    color: var(--color-ligth);
}

.about-content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-ligth);
}



/* =================================== */

@media screen and (max-width:1024px) {
    .about-container {
        display: grid;
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about-me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about-content p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width:600px) {
    .about-me {
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about-cards {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about-container {
        text-align: center;
    }
    .about-content p {
        margin: 1.5rem 0;
    }
}